import * as d3 from 'd3';
import { ID3Node, IImageDef } from '../interfaces';
import { Subject, fromEvent } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { D3NodeBasicParser } from './@items';

import { calcGFit, setPattern, addDefaultDefs, buildTree, IRootNode } from './d3x/d3x';
import { BaseType } from 'd3';

export interface ID3OrgChartOptions {
  nodeParser?: D3NodeBasicParser;
  data?: ID3Node[];
  backgroundColor?: string;
  defaultFont?: string;
  nodeHorizontalSpaceScale?: number;
  nodeVerticalSpaceScale?: number;
}

export class D3OrgChart {
  //#region Default Options
  protected options: ID3OrgChartOptions = {
    backgroundColor: '#03A3C5',
    nodeParser: new D3NodeBasicParser(),
    data: [],
    defaultFont: 'Tahoma',
    nodeHorizontalSpaceScale: 0.5,
    nodeVerticalSpaceScale: 0.8
  }
  //#endregion

  //#region  NODE PARSER
  protected _nodeParser: D3NodeBasicParser;
  get nodeParser(): D3NodeBasicParser {
    if (!this._nodeParser) this._nodeParser = new D3NodeBasicParser();
    return this._nodeParser;
  }
  set nodeParser(parser: D3NodeBasicParser) {
    this._nodeParser = parser;
    // this.render();
  }
  //#endregion

  //#region SVG components
  protected container: d3.Selection<HTMLElement, any, any, any>;
  protected svg: d3.Selection<SVGElement, unknown, HTMLElement, unknown>;
  protected tb: d3.Selection<any, any, any, any>;

  protected chart: d3.Selection<SVGGElement, any, BaseType, any>;
  // protected centerG: d3.Selection<any, any, any, any>;
  protected defs: d3.Selection<any, any, any, any>;

  protected lastTransform: any;
  //#endregion

  //#region DATA
  protected root: d3.HierarchyPointNode<ID3Node>;
  // protected allNodes: any;

  protected _data: ID3Node[] = [];
  get data(): ID3Node[] {
    return this._data || [];
  }

  set data(data: ID3Node[]) {
    this._data = data;
    // this.render()
  }
  //#endregion

  zoomFunc = d3.zoom().scaleExtent([0.1, 20]).on("zoom", d => this.zoomed());

  currentZoom: number = 1;
  treemap: d3.TreeLayout<ID3Node>;

  constructor(prContainer: HTMLElement, prOptions?: ID3OrgChartOptions) {
    const me = this;

    // init container
    me.container = d3.select(prContainer);

    //If Data argument passed - then set it
    if (prOptions?.data) me._data = prOptions.data;

    // setting parser
    me._nodeParser = prOptions?.nodeParser || me.options.nodeParser;

    // applying options
    me.options = Object.assign(me.options, prOptions);

    // monitor resize
    fromEvent(window, 'resize')
      .pipe(
        debounceTime(300)
      ).subscribe(
        () => {
          // HDC - VER this.prepareCanvas();
          this.render();
        }
      )
  }

  render() {
    const me = this;
    // preparing svg
    me.prepareCanvas();

    // if no data then return
    if (!me.data.length) return;

    // preparing data
    me.prepareData();

    // showing nodes
    me.showNodes();
    return this;
  }

  // preparing canvas
  protected prepareCanvas() {
    const me = this;

    //Drawing containers
    const containerRect = me.container.node().getBoundingClientRect();
    me.svg = me.container.selectAll<SVGElement, any>('svg')
      .data([{ id: 'svg' }], (d: { id: string }) => d.id)
      .join(
        enter =>
          enter
            .append('svg')
            .attr('class', 'svg-chart-container')
            .attr('font-family', me.options.defaultFont)
            // .call(d3.zoom().on("zoom", d => this.zoomed()))
            .call(this.zoomFunc)
            .on("dblclick.zoom", null)
            .attr('cursor', 'move')
            .style('background-color', me.options.backgroundColor),
        update =>
          update
            .attr('width', containerRect.width)
            .attr('height', containerRect.height)
            .attr('font-family', me.options.defaultFont)
            .style('background-color', me.options.backgroundColor)
      );

    //Add container g element
    me.chart = me.svg.selectAll<SVGGElement, any>('g.chart')
      .data([{ id: 'chart' }], (d: { id: string }) => d.id)
      .join(
        enter => enter
          .append('g')
          .attr('class', 'chart')
          .attr('transform', `translate(0,0)`),
        update => update
      )
    if (!me.lastTransform)
      me.svg.transition().duration(200).call(
        me.zoomFunc.transform,
        d3.zoomIdentity.translate(containerRect.width / 2, this.nodeParser.height).scale(1)
      );

    //Add container g element
    me.tb = me.svg.selectAll('g.toolbar')
      .data([{ id: 'toolbar' }], (d: { id: string }) => d.id)
      .join(
        enter => enter
          .append('g')
          .attr('class', 'toolbar')
          .attr('transform', `translate(10,10)`)
          .append('rect')
          .attr('width', 30)
          .attr('height', 30)
          .attr('fill', 'url(#img-fit-icon)')
          .attr('cursor', 'pointer')
          .on('click', () => me.fit()),
        update => update
      )
    me.prepareDefs();
  }

  // preparing defs
  prepareDefs() {
    const me = this;
    // defs
    me.defs = me.svg.selectAll('defs.globalDefs')
      .data([{ id: 'defs' }], (d: { id: string }) => d.id)
      .join(
        enter => {
          const defs = enter.append('defs').attr('class', 'globalDefs');
          addDefaultDefs(defs, {
            'expand-icon': {
              type: 'icon',
              backgroundColor: '#05C356',
              scale: 0.6,
              color: '#EAFA48',
              name: 'faPlus'
            },
            'collapse-icon': {
              type: 'icon',
              backgroundColor: '#D10303',
              scale: 0.6,
              color: '#EAFA48',
              name: 'faMinus'
            },
            'fit-icon': {
              type: 'icon',
              backgroundColor: '#074EF3',
              scale: 0.9,
              color: '#EAFA48',
              name: 'faExpand'
            },
            'default-node-img': me.nodeParser.defaultImage as IImageDef
          }, me.nodeParser)
          //addDefs(defs, me.defsElements);
          return defs
        },
        update => update
      )
  }

  // preparing data
  protected prepareData() {
    const me = this;

    // if no data return 
    if (!me.data.length) return;

    // Convert flat data to hierarchical
    if (!me.root) {
      try { // preventing multiple root
        me.root = d3.stratify<ID3Node>().id(({ nodeId }) => nodeId).parentId(({ parentNodeId }) => parentNodeId)
          (me.data) as d3.HierarchyPointNode<ID3Node>;
      } catch (err) {
        me.root = d3.stratify<ID3Node>().id(({ nodeId }) => nodeId).parentId(({ parentNodeId }) => parentNodeId)
          ([{
            nodeId: 'root',
            parentNodeId: '',
            title: 'Error',
            description: err.message || err,
            nodeImage: {
              type: 'base64',
              data: me.nodeParser.errorBase64Icon
            }
          }]) as d3.HierarchyPointNode<ID3Node>;
      }
    }

    // preparing treemap
    const containerRect = me.container.node().getBoundingClientRect();
    me.treemap = d3.tree<ID3Node>() //.size([containerRect.width || 250, containerRect.height])
      .nodeSize([
        (me.nodeParser.width + (this.nodeParser.width * me.options.nodeHorizontalSpaceScale || 0.5)) * 0,
        (me.nodeParser.height + (this.nodeParser.height * me.options.nodeVerticalSpaceScale || 0.8)) * 0
      ])
      .separation((a: d3.HierarchyPointNode<ID3Node>, b: d3.HierarchyPointNode<ID3Node>) => {
        return 1
      })
    // me.allNodes = me.treemap(me.root).descendants();
    me.checkExpanded(me.root);
  }

  // showing nodes
  protected showNodes(prNode: d3.HierarchyPointNode<ID3Node> = null) {
    const me = this;

    if (!prNode) prNode = me.root;
    const nodeRef = {
      x: prNode.x || 0,
      y: prNode.y || 0,
      parent: prNode.parent
    };

    // console.log('Node Before: ', nodeRef.x);
    const updatePosition: { x: number, y: number } = {
      x: prNode.x || 0,
      y: prNode.y || 0
    }

    //  Assigns the x and y position for the nodes
    const treeData = me.treemap(me.root);
    // it is necesary for scope 
    const drawNodes = (container) => me.nodeParser.drawNodes(container, me.onNodeClick);
    const drawCollapser = (nodeGroup: d3.Selection<SVGGElement, d3.HierarchyPointNode<ID3Node>, any, any>) => {
      nodeGroup.append('circle')
        .attr('class', 'collapser')
        .attr('cx', me.nodeParser.width / 2)
        .attr('cy', me.nodeParser.height)
        .attr('r', 15)
        .attr('stroke', 'black')
        .attr('stroke-width', 2)
        .on('click', (node) => {
          // console.log('Node X: ', node.x)
          d3.event.preventDefault();
          me.expand(node, true);
        });
    };
    // console.log('Node ref: ', prNode.x, nodeRef.x, nodeRef )
    const nodes = treeData.descendants();

    // probanding
    buildTree(
      me.root,
      {
        w: me.nodeParser.width + (this.nodeParser.width * me.options.nodeHorizontalSpaceScale || 0.5),
        h: me.nodeParser.height + (this.nodeParser.height * me.options.nodeVerticalSpaceScale || 0.8)
      }
    );

    // console.log('Nodes: ', nodes);
    // preparing all definitions for nodes
    me.defs.selectAll('pattern.node')
      .data(nodes, (d: d3.HierarchyPointNode<ID3Node>) => d.data.nodeId)
      .join(
        enter =>
          enter.filter(d => d.data.nodeImage != null)
            .append('pattern').attr('class', 'node')
            .each((nodeData, i, enterNodes) => {
              // adding pattern
              const pattern = d3.select(enterNodes[i]);
              setPattern(pattern, nodeData.data, me.nodeParser);
            }),
        update => update,
        exit => exit
          .transition()
          .duration(me.nodeParser.transitionDuration)
          .style("opacity", 0)
          .remove()
      )

    // rendering nodes
    const nodeStartPosition = (d: d3.HierarchyPointNode<ID3Node>) => {
      if (nodeRef) {
        return `translate(${nodeRef.x - (me.nodeParser.width / 2)},${nodeRef.y})`
      }
      if (!d.parent) return `translate(${d.x - (me.nodeParser.width / 2)},${d.y})`;
      return `translate(${d.parent.x - (me.nodeParser.width / 2)},${d.parent.y})`
    }

    const nodePosition = (params: { x: number, y: number }) =>
      `translate(${params.x - (me.nodeParser.width / 2)},${params.y})`;

    const expandIconVisible =
      (d: d3.HierarchyPointNode<ID3Node> & { _children?: any }) => (d.children || d._children) ? 'visible' : 'hidden';
    const expandIcon =
      (d: d3.HierarchyPointNode<ID3Node> & { _children?: any }) => expandIconVisible(d) == 'visible' ? (d.data.expanded ? `url(#img-collapse-icon)` : `url(#img-expand-icon)`) : '';


    me.chart.selectAll('g.node')
      .data(nodes, (d: d3.HierarchyPointNode<ID3Node>) => d.data.nodeId)
      .join(
        enter =>
          enter.append('g')
            .style("opacity", 0)
            .attr('class', 'node')
            .attr('cursor', 'pointer')
            .attr('transform', nodeStartPosition)
            .call(drawNodes)
            .call(drawCollapser)
            .on('dblclick', (node) => {
              const containerRect = me.container.node().getBoundingClientRect();
              me.traslateTo((containerRect.width / 2) - node.x, ((containerRect.height - me.nodeParser.height) / 2) - node.y)
            }),
        update => update,
        exit =>
          exit
            .transition()
            .duration(me.nodeParser.transitionDuration)
            .attr('transform', nodePosition(prNode))
            .style("opacity", 0)
            .remove()
      )
      .transition().duration(me.nodeParser.transitionDuration)
      .style("opacity", 1)
      .attr('transform', nodePosition)
      .selectAll('circle.collapser')
      .attr('visibility', expandIconVisible)
      .attr('fill', expandIcon)

    // rendering links
    const pathStartingDiagonal = (d: { x: number, y: number, parent: IRootNode<ID3Node> }) => {
      return this.linkPath(d, d)
    }

    const pathDiagonal = (d: IRootNode<ID3Node>) => {
      const target = { x: d.parent.x, y: d.parent.y + me.nodeParser.height };
      return this.linkPath(d, d.parent)
    }

    me.chart.selectAll('path.link')
      .data(nodes.slice(1), (d: d3.HierarchyPointNode<ID3Node>) => d.data.nodeId)
      .join(
        enter =>
          enter
            .insert('path', 'g')
            .attr('class', 'link')
            .attr('fill', 'none')
            .attr('stroke', 'blue')
            .attr('stroke-width', 2)
            .attr('d', pathStartingDiagonal(nodeRef)),
        update => update,
        exit =>
          exit
            .transition().duration(me.nodeParser.transitionDuration)
            .attr('d', pathStartingDiagonal(prNode))
            .remove()
      )
      .transition().duration(me.nodeParser.transitionDuration)
      .attr('d', pathDiagonal);
  }

  // Zoom handler function
  zoomed() {
    const me = this;
    // Saving d3 event's transform object
    me.lastTransform = d3.event.transform;
    // Reposition and rescale chart accordingly
    me.chart.attr('transform', d3.event.transform);
  }

  //#region Events
  // node click
  onNodeClick: Subject<{ id: string, node: ID3Node }> = new Subject();
  protected _onNodeClick(nodeId: string, node: ID3Node) {
    this.onNodeClick.next({ id: nodeId, node: node });
  }
  //#endregion

  // Generate custom diagonal - play with it here - https://observablehq.com/@bumbeishvili/curved-edges?collection=@bumbeishvili/work-components
  linkPath(source: { x: number, y: number, parent: IRootNode<ID3Node> }, target: { x: number, y: number, parent: IRootNode<ID3Node> }) {
    const me = this;

    // Calculate some variables based on source and target (s,t) coordinates
    let x = source.x;
    let y = source.y;
    const ex = target.x;
    const ey = target.y + me.nodeParser.height;

    const linkHeight = 25;
    const curveRadius = Math.abs(x - ex) != 0 ? 15 : 0;

    let xrvs = ex - x < 0 ? -1 : 1;
    let yrvs = ey - y < 0 ? -1 : 1;
    let w = Math.abs(ex - x) - (curveRadius * 2);

    const path = `
      M ${x} ${y}
      L ${x} ${y + (linkHeight * yrvs)}
      C ${x} ${y + ((linkHeight + curveRadius) * yrvs)} ${x} ${y + ((linkHeight + curveRadius) * yrvs)} ${x + (curveRadius * xrvs)} ${y + ((linkHeight + curveRadius) * yrvs)}
      L ${x + ((w + curveRadius) * xrvs)} ${y + ((linkHeight + curveRadius) * yrvs)}
      C ${ex}  ${y + ((linkHeight + curveRadius) * yrvs)} ${ex}  ${y + ((linkHeight + curveRadius) * yrvs)} ${ex} ${y + ((linkHeight + curveRadius) * yrvs) + (curveRadius * yrvs)}
      L ${ex} ${ey}
    `;
    return path;
  }

  checkExpanded(node: d3.HierarchyPointNode<ID3Node> & { _children?: any }) {
    const me = this;

    // checking expanded
    if (node.data.expanded) {
      if (!node.children && node._children) {
        node.children = node._children;
        node._children = null;
      }
    } else { // collapsed
      if (node.children) {
        node._children = node.children;
        node.children = null;
      }
    }
    // checking children
    (node.children || node._children || []).forEach(current => me.checkExpanded(current))
  }

  expand(node: d3.HierarchyPointNode<ID3Node> & { _children?: any }, toggle: boolean = false) { //,  render: boolean = false) {
    const me = this;

    const nodeRef = Object.assign(node);

    // if toggle - lets toggle
    if (toggle) node.data.expanded = !node.data.expanded;

    // checking expanded
    if (node.data.expanded) {
      if (!node.children && node._children) {
        node.children = node._children;
        node._children = null;
      }
    } else { // collapsed
      if (node.children) {
        node._children = node.children;
        node.children = null;
      }
    }

    // expand(node.children, node.data.expanded);
    // console.log('Before expand: ', nodeRef.x,  node.x)
    if (toggle) me.showNodes(node);
  }

  traslateTo(prX, prY) {
    const me = this;
    me.svg
      .transition().duration(me.nodeParser.transitionDuration)
      .call(
        me.zoomFunc.transform,
        d3.zoomIdentity.translate(prX, prY)
      );
  }

  fit() {
    const me = this;
    // const zoom = d3.zoom().on("zoom", d => this.zoomed())

    const fit = calcGFit(me.chart, me.svg, me.root, me.nodeParser)
    me.svg.transition().duration(me.nodeParser.transitionDuration).call(
      me.zoomFunc.transform,
      d3.zoomIdentity.translate(fit.x, fit.y).scale(fit.scale)
    );
  }

  setOptions(prOptions: Partial<ID3OrgChartOptions>) {
    const me = this;
    me.options = Object.assign(me.options, prOptions);
  }
}

